import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
interface LoginResponse {
  access_token: string;
  resultCode: number;
  message: string;
  users: UserType;
}
interface UserType {
  sub: string;
  email: string;
  gender: string;
  address: string;
  role: string;
  phone: string;
}
//kiểu của decoded
//kiểu trả về check auth
interface CheckAuthResult {
  decoded: UserType;
  resultCode: number;
}

interface AuthType {
  isLoggedIn: boolean;
  loading: boolean;
  users: UserType | null;
  error: string | null;
  result: LoginResponse | null;
}
const initialState: AuthType = {
  result: null,
  isLoggedIn: false,
  users: null,
  loading: false,
  error: null,
};

//login
export const fetchLogin = createAsyncThunk<
  LoginResponse,
  { signInput: string; password: string },
  { rejectValue: string }
>("auth/login", async ({ signInput, password }, thunkAPI) => {
  try {
    const response = await axios.post(`http://localhost:8080/api/auth/login`, {
      signInput,
      password,
    });
    if (response.data) {
      return response.data;
    }
  } catch (error) {
    if (axios.isAxiosError(error)) {
      return thunkAPI.rejectWithValue("Cant not post login");
    }
    return thunkAPI.rejectWithValue("Server error");
  }
});

//check auth
export const checkAuth = createAsyncThunk<
  CheckAuthResult,
  void,
  { rejectValue: string }
>("auth/check", async (_, thunkAPI) => {
  try {
    const response = await axios.get(`http://localhost:8080/api/auth/check`);
    if (response.data) {
      return response.data;
    }
  } catch (error) {
    if (axios.isAxiosError(error)) {
      return thunkAPI.rejectWithValue("Axios error");
    }
    return thunkAPI.rejectWithValue("Server error");
  }
});
//slice
const authSlice = createSlice({
  name: "auht",
  initialState,
  reducers: {},
  extraReducers: (builder) =>
    builder
      // login
      .addCase(fetchLogin.pending, (state) => {
        state.error = null;
        state.isLoggedIn = false;
        state.loading = true;
      })
      .addCase(fetchLogin.fulfilled, (state, action) => {
        state.result = action.payload;
        state.loading = false;
        state.isLoggedIn = true;
      })
      .addCase(fetchLogin.rejected, (state, action) => {
        state.loading = false;
        state.result = null;
        state.error = action.payload as string;
      })
      // check auth
      .addCase(checkAuth.pending, (state) => {
        state.error = null;
        state.isLoggedIn = false;
        state.loading = true;
        state.users = null;
      })
      .addCase(checkAuth.fulfilled, (state, action) => {
        state.loading = false;
        state.isLoggedIn = true;
        state.users = action.payload.decoded;
      })
      .addCase(checkAuth.rejected, (state, action) => {
        state.error = action.payload as string;
        state.loading = false;
        state.users = null;
        state.isLoggedIn = false;
      }),
});

export default authSlice.reducer;
